
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Masker from 'vux/src/components/masker/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Card from 'vux/src/components/card/index.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
phone:'',
showMenus: false,
fenqiList:[],
length:'',
state_money:0,
all_money:0,
dh_money:0,
}
},
computed: {

},
created(){
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
Group,
Cell,
CellBox,
XInput,
XButton,
Masker,
Box,
Grid,
GridItem,
Badge,
Card
},
methods:{
getData(){
let orderid = this.$route.query.orderid;
let postData = this.$qs.stringify({
orderid:orderid
})
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.orderInfo,
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.fenqiList = res.data.retData.fenqi.list;
this.length = res.data.retData.fenqi.list.length;
this.all_money = res.data.retData.fenqi.all_money;
this.state_money = res.data.retData.fenqi.state_money;
this.dh_money = res.data.retData.fenqi.dh_money;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goHome(){
this.$router.push('/home');
}
}
}



